import React from 'react';
import { Link } from 'react-router-dom';
import Walletconnect from "../../themes/Walletconnect";
import './Header.css';

function Header () {
    return (
        <header id="header">
            {/* Navbar */}
            <nav data-aos="zoom-out" data-aos-delay={800} className="navbar gameon-navbar navbar-expand">
                <div className="container header">
                    {/* Logo */}
                    <Link to="/" className="navbar-brand">
                        <img src="/img/ordium_logo.png" alt="Brand Logo" />
                    </Link>
                    <div className="ml-auto" />
                    {/* Navbar Nav */}
                    <ul className="navbar-nav items mx-auto" style={{marginLeft:"-2em"}}>
                        <li className="nav-item" style={{ marginRight: '1rem', marginTop:"0.3em" }}>
                            <Link to="/" className="nav-link" style={{fontSize:"1.3em"}}>Home</Link>
                        </li>
                        <li className="nav-item" style={{ marginRight: '15rem', marginTop:"0.3em" }}>
                            <Link to="/projects" className="nav-link" style={{fontSize:"1.3em"}}>IDOs</Link>
                        </li>
                        <li className="nav-item social" style={{ marginRight: '0.5rem', marginTop:"0.3em" }}>
                            <a href="https://twitter.com/ordium_io" style={{fontSize:"1.5em"}}>
                                <i className="icon-social-twitter" />
                            </a>
                        </li>
                        <li className="nav-item social" style={{ marginRight: '0.5rem', marginTop:"0.3em" }}>
                            <a href="https://ordium.gitbook.io/ordium/" style={{fontSize:"1.3em"}}>
                                <i class="fa-solid fa-book"></i>
                            </a>
                        </li>
                        <li className="nav-item social" style={{ marginTop:"0.3em" }}>
                            <a href="https://medium.com/@ordium/about" style={{fontSize:"1.3em"}}>
                                <i className="fab fa-medium" />
                            </a>
                        </li>

                    </ul>
                    {/* Navbar Toggler */}
                    <ul className="navbar-nav toggle">
                        <li className="nav-item">
                            <a href="#" className="nav-link" data-toggle="modal" data-target="#menu">
                                <i className="icon-menu m-0" />
                            </a>
                        </li>
                    </ul>
                    {/* WalletConnect */}
                    <ul className="navbar-nav action">
                        <Walletconnect />
                    </ul>
                </div>
            </nav>
            {/* <div className="social-icons d-flex justify-content-center my-3" style={{display:"none"}}>
                <li className="nav-item">
                    <a href="https://twitter.com/ordium_io" target="_blank" rel="noreferrer">
                        <i className="icon-social-twitter" />
                    </a>
                </li>
                <li className="nav-item">
                    <a href="https://twitter.com/ordium_io" target="_blank" rel="noreferrer">
                        <i className="icon-notebook" />
                    </a>
                </li>
                <li className="nav-item">
                    <a href="https://twitter.com/ordium_io" target="_blank" rel="noreferrer">
                        <i className="icon-book-open" />
                    </a>
                </li>
            </div> */}
        </header>
    );
};

export default Header;